.subscribe {
	position: relative;
	background-color: $light-gray;
	padding-top: 112px;
	min-width: 320px;
	&:before {
		content: '';
		position: absolute;
		height: 90px;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: $purple; }
	@include r(1199) {
		padding-top: 120px;
		&:before {
			height: 99px; } }
	@include r(767) {
		padding-top: 76px; }

	&__container {}

	&__newsletter {}

	&__form {
		position: relative;
		margin-top: 75px;
		@include r(767) {
			margin-top: 58px; } }

	&__title {
		line-height: 1; }

	&__button-row {}

	&__button {} }
