.how-it-works {
	position: relative;
	color: $gray;
	min-width: 320px;
	@include markpro;

	&__wrapper {
		padding-top: 90px;
		padding-bottom: 120px;
		margin-top: -163px;
		margin-bottom: -75px;
		@include r(991) {
			padding-top: 76px; }
		@include r(767) {
			margin-top: -75px;
			padding-top: 60px;
			padding-bottom: 70px;
			margin-bottom: -16px; } }

	&__title {
		text-align: center; }

	&__text-wrapper {
		display: flex;
		justify-content: space-between;
		margin-top: 6px;
		@include r(767) {
			flex-wrap: wrap; } }

	&__text {
		width: 49%;
		font-size: 21px;
		line-height: 1.46;
		letter-spacing: -1px;
		@include r(767) {
			width: 100%;
			font-size: 16px; } }

	&__images {}

	&__list {}

	&__item {
		display: flex;
		justify-content: space-between;
		margin-bottom: 50px;
		@include r(991) {
			flex-wrap: wrap; } }

	&__item-title {
		position: relative;
		width: 41%;
		font-size: 24px;
		font-weight: 700;
		line-height: 1.2;
		align-self: flex-start;
		&:after {
			position: absolute;
			content: '';
			bottom: -23px;
			left: 0;
			width: 52px;
			height: 2px;
			background-color: #003580; }
		@include r(991) {
			width: 100%;
			padding-bottom: 20px;
			&:after {
				bottom: 0; } } }



	&__descr {
		font-size: 21px;
		line-height: 1.46;
		width: 57%;
		@include r(991) {
			width: 100%;
			margin-top: 12px; } }

	&__button {
		@include r(767) {
			&:after {
				margin-left: 12px; } } } }
