.client-list {
	padding-top: 39px;
	padding-bottom: 168px;
	@include r(1199) {
		padding-top: 29px; }
	@include r(767) {
		padding-top: 40px; }

	&__container {}

	&__clients {}

	&__wrapper {
		&:not(:first-child) {
			margin-top: 94px; }

		@include r(767) {
			&:not(:first-child) {
				margin-top: 53px; } } }

	&__title {
		margin-bottom: 14px; }

	&__list {}

	&__item {
		display: flex;
		align-items: center;
		margin-top: 30px;
		@include r(767) {
			margin-top: 19px; } }

	&__button-wrapper {
		display: flex;
		justify-content: flex-end;
		margin-top: 65px; }

	&__button {
		background-color: transparent; } }

