.about {
	height: 900px;
	padding-top: 262px;
	padding-bottom: 280px;
	min-width: 320px;
	@include r(991) {
		padding-top: 174px;
		padding-bottom: 136px; }
	@include r(767) {
		padding-top: 149px;
		padding-bottom: 183px;
		height: 568px; }
	@include r(480) {
		padding-top: 75px; }

	&__container {
		height: 100%; }

	&__text {
		@include markpro;
		font-size: 21px;
		font-weight: 700;
		line-height: 1.33;
		color: $white;
		text-align: center;
		@include r(767) {
			font-size: 16px; } }

	&__title {
		text-align: center;
		padding: 0 40px;
		margin-top: auto;
		@include r(1199) {
			font-size: 60px; }
		@include r(767) {
			padding: 0; } }

	&__row {
		height: 100%; }

	&__col {}

	&__text-wrapper {
		height: 100%;
		display: flex;
		flex-direction: column; }

	&__button-row {
		margin-top: auto; }

	&__button {
		display: inline; } }
