.contact-us {
	position: relative;
	background-color: $light-gray;
	padding-top: 60px;
	min-width: 320px;
	&:before {
		content: '';
		position: absolute;
		height: 210px;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: $purple; }
	@include r(1199) {
		padding-top: 120px;
		&:before {
			height: 128px; } }
	@include r(991) {
		&:before {
			height: 135px; } }
	@include r(767) {
		padding-top: 76px;
		&:before {
			height: 42px; } }

	&__container {}

	&__row {}

	&__col {}

	&__card {}

	&__title {}

	&__subtitle {
		@include markpro;
		color: $gray;
		font-size: 21px;
		line-height: 1.46; }

	&__form {
		margin-top: 36px; }



	&__button-row {}

	&__button {} }
