.career-card {
	background-color: $white;
	padding: 50px 22px;
	width: calc(33.3% - 30px);
	margin-left: 30px;
	margin-top: 30px;
	min-height: 440px;
	display: flex;
	flex-direction: column;
	@include r(1199) {
		min-height: 477px; }
	@include r(991) {
		width: calc(50% - 30px);
		min-height: 435px; }
	@include r(767) {
		width: 100%;
		margin-left: 0;
		min-height: 477px;
		margin-top: 10px; }

	&__title {
		@include markpro;
		font-weight: 700;
		font-size: 24px; }

	&__descr {
		margin-top: 16px;
		@include markpro;
		font-size: 24px; }

	&__button {
		margin-top: auto; } }
