.breadcrumbs {
	padding: 30px 0;
	@include r(767) {
		display: none; }

	&__container {}

	&__list {
		display: flex;
		justify-content: flex-end; }

	&__item {}

	&__link {
		position: relative;
		@include markpro;
		font-size: 12px;
		font-weight: 600;
		letter-spacing: 1.71px;
		color: $blue;
		display: flex;
		margin-right: 24px;
		&:after {
			content: '';
			position: absolute;
			display: block;
			width: 2px;
			height: 2px;
			background-color: $blue;
			right: -14px;
			top: calc(50% - 1px); }
		&:hover {
			color: rgba($blue, .85); } }


	&__link_current {
		pointer-events: none;
		opacity: .2;
		margin-right: 0;
		&:after {
			display: none; } } }
